@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --primary-color: #006405;
  --primary-color-menu-hover: #077e2b;
  --primary-color-selected: #7fc393;
  --primary-color-hover: #c3e4cd;
  --secondary-color: #af1a1a;
  --secondary-color-accent: white;
  --primary-color-disable: #ccc;
  --primary-color-accent: white;
  --primary-table-alternating: #eee;
  --normal: 100;
  --light: 300;
  --regular: 400;
  --demibold: 700;
  --bold: 900;
}

/* width */
::-webkit-scrollbar {
  width: 0.5em;
}

/* Track */
::-webkit-scrollbar-track {
  background: #c3e4cd;
  background: var(--primary-color-hover);
  border: 1px solid white;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #077e2b;
  background: var(--primary-color-menu-hover);
  border: 1px solid white;
  border-radius: 0.3em;
}

::-webkit-scrollbar-thumb:hover {
  background: #006405;
  background: var(--primary-color);
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  background: #f2f2f2;
}

.nav-menu:focus + .menus,
.menus:hover {
  opacity: 1;
  height: unset;
  width: 100%;
  z-index: 99;
  -webkit-transform: scale(1);
          transform: scale(1);
  transition: opacity ease-in-out 0.3s;
}

.menus {
  display: flex;
  width: 100%;
  -webkit-transform: scale(0);
          transform: scale(0);
  overflow: hidden;
  position: fixed;
  z-index: -1;
  background-color: #006405;
  background-color: var(--primary-color);
  left: 0;
  justify-content: center;
  opacity: 0;
}

.menus .menu-items {
  margin: 1em;
}

.menus .menu-items .head {
  text-align: center;
  font-size: 1.3em;
  font-weight: 900;
  font-weight: var(--bold);
  color: white;
  color: var(--primary-color-accent);
}

.menus .menu-items .navs a {
  font-size: 1em;
}

.stats-container {
  width: calc(100% - 2em);
  box-sizing: border-box;
  box-shadow: 0em 0em 0.2em 0em #006405;
          box-shadow: 0em 0em 0.2em 0em var(--primary-color);
  margin: 1em;
  position: relative;
  padding: 0.5em;
  border-radius: 0.1em;
  border-end-end-radius: 0.5em;
  border-top-left-radius: 0.5em;
}

.stats-container .bar {
  position: absolute;
  background-color: #7fc393;
  background-color: var(--primary-color-selected);
  top: 0.2em;
  left: 0.2em;
  z-index: -1;
  bottom: 0.2em;
  border-end-end-radius: 0.5em;
  border-top-left-radius: 0.5em;
}

.stats-container .full {
  background-color: #006405 !important;
  background-color: var(--primary-color) !important;
}

.stats-container .full + div {
  color: white;
  color: var(--primary-color-accent);
}

.stats-container .data {
  display: flex;
  justify-content: space-between;
  padding: 0.5em;
}

.stats-container .data .barangay {
  font-weight: 700;
  font-weight: var(--demibold);
}

.stats-container .data .stats {
  display: flex;
}

.stats-container .data .stats .percentage {
  color: #aaa;
  font-weight: 700;
  font-weight: var(--demibold);
  margin-left: 0.4em;
}

.webcam-container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.webcam-container img {
  margin-right: 1em;
}

.center-footer {
  justify-content: center !important;
}

.center-footer .btn {
  margin: 0.5em;
}

.signature-container {
  position: relative;
  margin: 1em;
  display: flex;
  justify-content: center;
  align-items: center;
}

.signature-container::before {
  content: '';
  position: absolute;
  left: 1.5em;
  bottom: 10em;
  background-color: #ccc;
  width: calc(100% - 3em);
  height: 4px;
  border-radius: 1em;
  opacity: 0.3;
  z-index: 1;
}

.signature-container .name {
  position: absolute;
  font-size: 3em;
  top: 5em;
  font-weight: bold;
  color: #006405;
  color: var(--primary-color);
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  z-index: 1;
}

.signature-container .sigCanvas {
  box-shadow: 0em 0em 0.4em 0em #006405;
          box-shadow: 0em 0em 0.4em 0em var(--primary-color);
  border-radius: 0.3;
  z-index: 2;
}

.scanner-main-container {
  height: 89vh;
  width: 100%;
}

.scanner-main-container .scanner-container {
