  font-weight: 700;
  font-weight: var(--demibold);
}

.scanner-main-container .scanner-container .scanner-content .scanner-voters-info div {
  font-weight: 700;
  font-weight: var(--demibold);
}

.scanner-main-container .scanner-container .scanner-content .scanner-voters-info .name {
  font-size: 1.4em;
  font-weight: --bold !important;
}

.scanner-main-container .scanner-container .scanner-content .scanner-voters-info .scanner-title {
  opacity: 0.6;
  font-size: 0.7em;
  font-style: italic;
}

.leader-name {
  font-weight: 700;
  font-weight: var(--demibold);
}

