textarea,
input {
  border: none;
  border-bottom: 1px solid #006405;
  border-bottom: 1px solid var(--primary-color);
}

textarea:focus,
input:focus {
  outline: none;
}

.login-container {
  position: relative;
  background: white;
  margin: 10em auto;
  width: 20em;
  padding-bottom: 0.2em;
  border-radius: 0.5em;
  box-shadow: 0em 0em 0.5em 0em #ccc;
