}

.custom-input {
  padding: 0.3em;
  display: flex;
  overflow-y: auto;
  flex-direction: column;
  width: 100%;
  height: 2.7em;
  box-sizing: border-box;
  text-align: left;
  justify-content: space-between;
  position: relative;
  overflow-y: hidden;
}

.custom-input label {
  font-size: 0.55em;
  font-weight: 900;
  font-weight: var(--bold);
  color: gray;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.custom-input select {
  font-size: 0.7em;
  height: 100%;
  width: 100%;
  border: none;
  border-bottom: 1px solid #006405;
