}

.custom-input .datepicker-container {
  position: relative;
  width: 100%;
  height: 100%;
}

.custom-input .datepicker-container .custom-datepicker {
  background: none;
  text-decoration: none;
  cursor: pointer;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  text-align: left;
  font-size: 0.7em;
}

.custom-input .datepicker-container .custom-datepicker .react-datetime-picker__wrapper {
  border: 0px;
  padding: 0em !important;
  border-bottom: 1px solid #006405 !important;
  border-bottom: 1px solid var(--primary-color) !important;
  margin-bottom: 0.9em;
}

.custom-input .datepicker-container .custom-datepicker .react-datetime-picker__clear-button {
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
}

.custom-input .select-container {
  position: relative;
  width: 100%;
  height: 100%;
}

.custom-input .select-container .current-selection-icon {
  position: absolute;
  margin: 0;
  right: 0.3em;
  bottom: 0.4em;
  font-size: 0.7em;
  cursor: pointer;
}

.custom-input .select-container .current-selection {
  background: none;
  border: none;
  text-decoration: none;
  cursor: pointer;
  border-bottom: 1px solid #006405;
  border-bottom: 1px solid var(--primary-color);
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  text-align: left;
  font-size: 0.7em;
  padding: 0em;
}

.custom-input .select-container .current-selection:focus {
  outline: none;
}

.custom-input .select-container .selection {
  width: 100%;
  z-index: 10;
  position: absolute;
  opacity: 0;
  scrollbar-width: thin;
  border-radius: 0.3em;
  background-color: white;
  box-shadow: 0em 0.2em 0.3em 0em #ccc;
  left: 0;
  height: 0;
  top: calc(100% + 0.2em);
  overflow: auto;
}

.custom-input .select-container .selection:hover {
  opacity: 1;
  height: unset;
  max-height: 10em;
  overflow: auto;
}

.custom-input .select-container .selection div {
  color: black;
  padding: 10px;
  width: 100%;
  box-sizing: border-box;
  text-align: left;
  display: block;
  cursor: pointer;
}

.custom-input .select-container .selection div:hover:not(.active) {
  background: #c3e4cd;
  background: var(--primary-color-hover);
}

.custom-input .select-container .selection .active {
  background: #006405;
  background: var(--primary-color);
  color: white;
  color: var(--primary-color-accent);
}

.custom-input:focus-within .current-selection-icon,
.custom-input:focus-within label {
  color: #006405;
  color: var(--primary-color);
  font-weight: 900;
  font-weight: var(--bold);
}

.custom-input:focus-within .selection,
.custom-input:focus-within .selection:hover {
  opacity: 1;
  height: unset;
  max-height: 10em;
  overflow: auto;
}

.loading-screen {
  display: none;
}

.loading-block {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: block;
  z-index: 99999;
}

.loading-block .loading-blocker {
  z-index: 999999;
  background-color: #aaa;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  position: fixed;
  opacity: 0.5;
  top: 0;
  left: 0;
}

.loading-block .loading {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  justify-content: center;
  align-items: center;
  display: flex;
}

.loading-block .loading label {
  position: relative;
  color: #006405;
  color: var(--primary-color);
  font-size: 1.3em;
  font-weight: 900;
  font-weight: var(--bold);
  z-index: 9999999;
  transition: -webkit-transform 350ms ease-in-out;
  transition: transform 350ms ease-in-out;
  transition: transform 350ms ease-in-out, -webkit-transform 350ms ease-in-out;
  cursor: progress;
  text-transform: uppercase;
}

.loading-block .loading label:before {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  background: #006405;
  background: var(--primary-color);
  height: 0.1em;
  border-radius: 5em;
  -webkit-animation: showhide 1.5s alternate infinite;
          animation: showhide 1.5s alternate infinite;
}

.loading-block .loading label:hover {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}

@-webkit-keyframes showhide {
  from {
    -webkit-transform: scaley(0);
            transform: scaley(0);
  }
  to {
    -webkit-transform: scaley(1);
            transform: scaley(1);
  }
}

@keyframes showhide {
  from {
    -webkit-transform: scaley(0);
            transform: scaley(0);
  }
  to {
    -webkit-transform: scaley(1);
            transform: scaley(1);
  }
}

.main-container {
  width: 100%;
  position: relative;
}

.main-container .header {
  margin: 0;
  padding: 1em;
  font-size: 1em;
  background: white;
}

.search-container {
  background: white;
  position: relative;
  display: flex;
  padding: 0.2em 0.5em;
  margin: 0.8em 0.3em;
  box-shadow: 0em 0em 0.2em 0.01em #ccc;
  border-radius: 5em;
}

.search-container input {
  flex-grow: 1;
  border: none;
  font-size: 0.7em;
}

.search-container .search-icon {
  margin: 0.5em;
  font-size: 0.8em;
  cursor: pointer;
  color: #006405;
  color: var(--primary-color);
}

.no-padding-top-container {
  padding-top: 0 !important;
}

.item-container {
  background: white;
  padding: 0.5em;
  position: relative;
  box-shadow: 0em 0em 0.2em 0em #ccc;
  margin: 0.2em;
}

.pagination {
  float: right;
  display: flex;
